<template>
  <div class="detail">
    <van-nav-bar :title="im.name" left-text="返回" left-arrow @click-left="onBack" fixed />
    <h4 v-text="im.name"></h4>
    <img :src="im.img" />
    <van-goods-action>
      <van-goods-action-icon icon="chat-o" text="客服" />
      <van-goods-action-icon icon="cart-o" text="购物车"  />
      <van-goods-action-icon icon="shop-o" text="店铺" />
      <van-goods-action-button type="danger" text="立即购买" @click="joinCrat" />
    </van-goods-action>
  </div>
</template>

<script>
import { GoodsAction, GoodsActionButton, GoodsActionIcon, NavBar ,Toast} from "vant";
export default {
  name: "Detail",
  props: {
    id: {
      type: String,
      required: true
    }
  },
  data: function() {
    return {
      im: {}
    };
  },
  components: {
    [GoodsAction.name]: GoodsAction,
    [GoodsActionButton.name]: GoodsActionButton,
    [GoodsActionIcon.name]: GoodsActionIcon,
    [NavBar.name]:NavBar,
    [Toast.name]:Toast
  },
  methods: {
    // 加入购物车
    joinCrat() {
      let data={
        good_id: this.$route.params.id, //当前路由里面的id
      }
      this.$http.addToCart(data).then(res=>{
        Toast(res)
         this.timer = setTimeout(()=>{
          this.$router.push('/cart')
        },1500)
      })
    },
    beforeDestroy() {
    clearTimeout(this.timer)
  },
    // 返回首页
    onBack(){
      this.$router.replace("/SouY")
    }
  },
  mounted() {
    // 获取商品详情
    let params = {
      good_id: this.$route.params.id
    };
    this.$http.getGoodDetail(params).then(res => {
      console.log("datail", res);
      this.im = res;
      console.log(this.im);
    });
  }
};
</script>

<style lang='scss' scoped>
.detail{
  &>img{
    display: block;
    width:5rem;
    height: 5rem;
  }
}
</style>